$bc: #16181a;
$theme: #1f8261;

body {
  margin: 0;
  background-color: $bc;
  font-weight: 100;
  color: white;
  a {
    color: white;
    text-decoration: none;
  }
  h1, h2, h3, h4, p {
    margin: 0;
    &:not(p) {
      font-weight: 400;
    }
  }
  ul ul {
    padding: 10px 40px;
  }
  li::marker {
    color: $theme;
  }
  hr {
    opacity: 0.1;
    margin: 1em 0;
  }
}

.color {
  color: $theme;
}

.bold {
  font-weight: 400;
}

.hoverlink {
  transition: 0.3s;
  cursor: pointer;
  &:hover {
    color: white;
  }
}
.button {
  padding: 10px 30px;
  border: 1px solid $theme;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 12px;
  width: fit-content;
  &:hover {
    background-color: $theme;
  }
}

@keyframes shake {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(5deg);
  }
  75% {
    transform: rotate(-8deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.shake {
  animation: shake 0.5s ease-in-out;
}

#App {
  display: flex;
  font-size: 120%;
  font-family: 'Segoe UI', 'Noto Sans', 'Roboto';
}

#Nav {
  background-color: $theme;
  padding: 10px;
  color: white;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
  height: 100vh;
  overflow: hidden;
  transition: 0.3s;
  width: 40px;
  position: fixed;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  z-index: 10;
  height: 100%;
  // @media (max-width:500px) {
  //   flex-direction: row-reverse;
  // }
  
  #header {
    opacity: 0;
    text-wrap: nowrap;
    margin: 30px 10px;
    transition: 0.3s;
  }
  
  #link {
    padding: 5px 10px;
    display: flex;
    align-items: center;
    gap: 10px;
    transition: 0.3s;
    #icon {
      transition: 0.3s;
      text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.3);
    }
    span {
      width: 0;
      opacity: 0;
      transition: opacity 0.3s;
      &.bold {
        font-weight: 400;
      }
    }
    &:hover {
      #icon {
        transform: rotate(-10deg);
      }
      margin-left: 10px;
    }
  }
    &.navshown {
      width: 180px;
      #link span, #header {
        opacity: 1;
      }
    }
}

#App>div:not(:first-child) {
  margin-left: 60px;
  padding: 40px 80px;
  font-size: 110%;
  display: flex;
  flex-direction: column;
  gap: 50px;
  @media (max-width:500px) {
    gap: 20px;
  }
  #content h3 {
    margin-bottom: 10px;
  }
  #icon {
    font-size: 80%;
  }
  @media (max-width:500px) {
    padding: 20px 40px;
  }
}

#Home {
  width: 100%;
  #header {
    display: flex;
    flex-direction: column;
    gap: 10px;
    h1>#icon {
      @keyframes wave {
        0%, 40%, 100% {
          transform: rotate(-20deg);
        }
        20% {
          transform: rotate(20deg) translateX(10px) translateY(-5px);
        }
       
      }
      animation: wave 2s infinite ease-in-out;
    }
  }
  #links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    a {
      width: 200px;
      text-align: center;
      border: 1px solid $theme;
      padding: 10px 20px;
      border-radius: 12px;
      transition: 0.4s;
      &:hover {
        background-color: $theme;
        color: $bc;
      }
    }
  }
}

#Projects {
  #project {
    display: flex;
    gap: 50px;
    #left {
      width: 50%;
      >#title {
        margin-bottom: 10px;
        #title {
          transition: 0.3s;
          &:hover {
            color: $theme;
          }
        }
        #date {
          opacity: 0.5;
          font-size: 80%;
        }
      }
      #content {
        .button {
          margin-top: 50px;
          margin-right: 20px;
          @media (max-width:600px) {
            margin-top: 20px;
          }
          display: block;
        }
        #buttons {
          display: flex;
          flex-wrap: wrap;
        }
      }
    }
    #image {
      width: 50%;
      img {
        max-width: 100%;
        border-radius: 10px;
        box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
      }
    }
    @media (max-width:1000px) {
      flex-direction: column;
      #left, #image {
        width: unset;
      }
    }
    
  }
}

#Project {
  #header {
    #date {
        opacity: 0.5;
        font-size: 80%;
        margin-bottom: 1em;
      }
    #subheader {
      display: flex;
      gap: 40px;
      #logo {
        max-width: 20%;
        max-height: 120px;
        height: fit-content;
        width: fit-content;
        margin: 20px 40px;
        align-self: center;
      }
      @media (max-width:1000px) {
        flex-direction: column-reverse;
        #logo {
          max-width: unset;
          margin: none;
        }
      }
    }
  }
  #content {
    display: grid;
    transition: 0.3s;
    gap: 40px;
    grid-template-columns: 5fr 2fr;
    @media (max-width:800px) {
      grid-template-columns: 1fr;
    }
    #images {
      order: -1;
      grid-template-columns: 5fr 2fr;
        @media (max-width:800px) {
          order: unset;
        }
      display: flex;
      flex-direction: column;
      gap: 30px;
      img {
        width: 100%;
        border-radius: 10px;
        box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
      }
    }
    #more {
      h3 #icon {
        transition: 0.5s;
        cursor: pointer;
        &:hover {
          color: white;
        }
        &:not(.shown) {
          transform: rotate(-180deg);
        }
      }
      #wrapper {
        transition: 0.3s;
        overflow: hidden;
      }
      #link {
        display: block;
        margin-bottom: 20px;
      }
    }
  }
}

#Contact {
  width: 100%;
  #contacts {
    margin: 0 10%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
      @media (max-width:1000px) {
        grid-template-columns: 1fr;
        gap: 50px;
      }
    #contact {
      display: flex;
      flex-direction: column;
      gap: 10px;
      align-items: center;
      #icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border: 1px solid $theme;
        padding: 20px;
        font-size: 150%;
        border-radius: 50%;
        transition: 0.3s;
      }
      &:has(#icon:hover), &:has(#name:hover) {
        #icon {
          background-color: $theme;
        }
      }
      #about {
        margin-top: 20px;
        font-size: 80%;
        opacity: 0.5;
      }
    }
  }
}

#About {
  p {
      margin: 15px 0;
    }
  #main {
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
    @media (max-width:500px) {
        flex-direction: column;
      }
    #pfp {
      max-width: 250px;
      height: fit-content;
      border-radius: 50%;
      margin: 0 50px;
      box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
      transform: rotate(-10deg);
    }
  }
  #hobbies {
    display: flex;
    flex-direction: column;
    gap: 30px;
    #art {
      display: flex;
      gap: 60px;
      @media (max-width:1200px) {
        flex-direction: column;
        align-items: center;
        gap: 20px;
      }
      #images {
        display: grid;
        grid-template-rows: repeat(2, 200px);
        @media (max-width:500px) {
            grid-template-rows: repeat(2, 100px);
          }
        gap: 10px;
        grid-template-areas:
            "image1 image2"
            "image1 image3";
        img {
          box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
          border-radius: 10px;
          max-height: 100%;
          
          &#image1 {
            grid-area: image1;
            height: 80%;
            margin-top: 10%;
          }
        }
      }
    }
  }
  
}